<template>
  <div class="todo-header">
    <input @keyup.enter="add"  type="text" v-model="title" placeholder="请输入你的任务名称，按回车键确认" />
  </div>
</template>

<script>
import {nanoid} from 'nanoid';
export default {
  name: "MyHeader",
    props: ['todoAdd'],
  data() {
      return {
          title: ''
      }
  },
  methods: {
      add(e){
          if(!this.title.trim()) return alert('输入不能为空')
          // 将用户的输入包装成一个对象
          const todoObj = {id:nanoid(),title: e.target.value,done: false}
          //通知组件去添加一个对象
          this.todoAdd(todoObj)
          //清空输入
          this.title = ''
      },
  },

};
</script>

<style scoped>
.todo-header input {
  width: 560px;
  height: 28px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 7px;
}

.todo-header input:focus {
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(82, 168, 236, 0.6);
}

</style>