<template>
  <li >
    <label>
      <input type="checkbox" :checked="item.done" @change="handleCheck(item.id)"/>
      <span>{{item.title}}</span>
    </label>
    <button class="btn btn-danger" @click="handleDelete(item.id)">删除</button>
  </li>
</template>

<script>
    export default {
        name: "MyItem",
        props: ['item','checkTodo','deleteTodo'],
        data() {
            return {
                
            }
        },
        methods: {
            //取消勾选
            handleCheck(id){
                // console.log(id);
                this.checkTodo(id)
            },
            //删除
            handleDelete(id){
                if(confirm('确定删除吗？')){
                    console.log(id);
                    this.deleteTodo(id)
                }
            }
        },
        mounted() {
            // console.log(this.item);
        },
    };
</script>

<style scoped>
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}
li:hover {
    background: #ccc;
}
li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  display: none;
  margin-top: 3px;
}
li:hover button{
    display: block;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}
</style>